
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup {
  position: relative;
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  overflow: auto;
  box-sizing: border-box;
  transition: all 0.3s ease;
}

.popup-header {
  position: absolute;
  top: 5px;
  right: 5px;
}

.close-btn {
  width: 23px;
  height: 23px;
  border-radius: 50%;
  background-color: #00000042;
  border: none;
  cursor: pointer;
  transition: background 0.2s;
}

.close-btn:hover {
  background-color: #00000060;
}

/* 全屏模式样式 */
.popup.fullscreen {
  width: 100vw !important;
  height: 100vh !important;
  max-width: none;
  border-radius: 0;
}

.popup-body {
  width: 100%;
  box-sizing: border-box;
  margin-top: 10px; /* 防止内容被关闭按钮遮挡 */
}

/* 悬停效果 */
.close-btn:hover,
.fullscreen-btn:hover {
  background-color: #00000060;
}
